import * as React from 'react';
import { graphql } from 'gatsby';
import Box from '@mui/material/Box';
import { Layout } from '../components/layout';
import { Seo } from '../components/seo';
import { Navigation } from '../components/navigation';
import { Mainvisual } from '../components/mainvisual';
import { Projects } from '../components/projects';

interface IProps {
  data: any;
  location: any;
}

const Page = ({ data, location }: IProps) => {
  const duration: number = 1500;
  const delay: number = 250;

  const [isLoaded, setIsLoaded] = React.useState(false);
  const [isEnded, setIsEnded] = React.useState(false);

  const onLoad = () => {
    setIsLoaded(true);
    setTimeout(() => {
      setIsEnded(true);
    }, duration + delay);
  };

  React.useEffect(() => {
    if (!window || !location) return;
    const searchParams = new URLSearchParams(location.search);
    if (searchParams.has('type', 'qrcode')) {
      const param = new URLSearchParams({
        utm_source: 'qrcode',
        utm_medium: 'namecard',
        utm_campaign: 'namecard'
      });
      window.location.replace(`/?${param.toString()}`);
    }
  }, [location]);

  React.useEffect(() => {
    if (document.readyState === 'complete') {
      onLoad();
      return;
    }

    window.addEventListener('load', onLoad);

    return () => {
      window.removeEventListener('load', onLoad);
    };
  }, []);

  return (
    <Layout>
      {!isEnded && (
        <Box
          sx={{
            position: 'fixed',
            top: 0,
            left: 0,
            width: '100%',
            height: '100vh',
            backgroundColor: '#eee',
            transition: `opacity ${duration}ms ease-out ${delay}ms`,
            opacity: isLoaded ? 0 : 1,
            pointerEvents: 'none',
            zIndex: 2000
          }}
        />
      )}
      <Navigation />
      <Mainvisual images={data.mainVisual} />
      <Projects />
    </Layout>
  );
};

export const query = graphql`
  {
    mainVisual: allFile(
      sort: { relativePath: ASC }
      filter: { relativeDirectory: { eq: "mainvisuals" } }
    ) {
      edges {
        node {
          name
          relativeDirectory
          relativePath
          childImageSharp {
            gatsbyImageData(width: 1920)
          }
        }
      }
    }
  }
`;

export default Page;

export const Head = () => <Seo />;
